@import url("https://fonts.cdnfonts.com/css/montserrat");
@mixin flex(
  $direct: row,
  $align: center,
  $justify: center,
  $wrap: no-wrap,
  $gap: 0
) {
  display: flex;
  flex-direction: $direct;
  align-items: $align;
  justify-content: $justify;
  flex-wrap: $wrap;
  gap: $gap;
}

@mixin font($size: 20px, $weight: 500, $color: #ffffff) {
  color: $color;
  font-size: $size;
  font-weight: $weight;
  font-style: normal;
  line-height: normal;
}

html {
  scroll-behavior: smooth;
}
* {
  margin: 0;
  box-sizing: border-box;
  font-family: "Montserrat", sans-serif;
}
body {
  background: url(./img/background.png);
  background-position: center;
  background-repeat: no-repeat;
  background-attachment: fixed;
}
.container {
  margin: 0 auto;
  max-width: 1440px;
}
header,
main,
footer {
  width: 100%;
}
a {
  text-decoration: none;
  @include font(22px, 600, #e9ecef);
}
ul {
  @include flex($justify: start, $gap: 30px);
  list-style: none;
  padding: 0;
}
h2 {
  @include font(38px, 600, #000);
}
h3 {
  @include font(25px, 600, #000);
}
hr {
  width: 100%;
}
/* Hamburger */
.hamburger {
  display: none;
  cursor: pointer;
  width: fit-content;
}
.bar {
  display: block;
  width: 25px;
  height: 3px;
  border-radius: 5px;
  margin: 5px auto;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  background-color: #fff;
}
.nav-link {
  transition: 0.7s ease;
}

/* Header */
header {
  background: #0d0c16;
  padding: 17px 0;
}
.header__wrapper {
  @include flex($justify: space-between, $gap: 0);
  padding-left: calc(5vw + 20px);
  padding-right: calc(5vw + 20px);
}
.header__logo {
  @include flex($justify: center, $gap: calc(1vw + 5px));
  .logo {
    width: 40px;
    height: 40px;
  }
}
.languages {
  cursor: pointer;
  @include flex($justify: center, $gap: calc(1vw));
}

/* Main */
.main__wrapper {
  @include flex($justify: start, $gap: calc(10vw + 20px));
  padding: calc(15vh + 20px) calc(5vw + 20px);
}
.main-text {
  @include flex($direct: column, $align: start, $justify: center, $gap: 18px);
  h1 {
    @include font(50px, 700, #212529);
  }
  p {
    @include font(40px, 600, #212529);
  }
}
.text-name {
  color: #580055;
}
.text-profession {
  color: #ae9eff;
}
.main-image {
  img {
    width: 400px;
    height: 400px;
    border-radius: 20px;
  }
}

/* About */
.about__wrapper {
  text-align: center;
  @include flex($justify: start, $direct: column, $gap: 40px);
  min-height: 90vh;
  padding: 100px calc(5vw + 20px) 0;
  p {
    @include font(18px, 400, #000);
  }
}
.about__github-user {
  @include flex($direct: column, $justify: space-between, $gap: 10px);
  img {
    border-radius: 50%;
    width: calc(200px + 2vw);
    height: calc(200px + 2vw);
  }
  padding: 20px;
  background: #ffffff;
  border: 1px dashed #000;
  border-radius: 10px;
}
.user-data {
  @include flex($direct: column, $justify: space-between, $gap: 5px);
  .github-name,
  .github-location,
  .github-date,
  .github-update {
    @include font($size: 16px, $weight: 400, $color: #000000);
  }
  .github-name {
    font-size: 18px;
  }
  a {
    @include font($size: 20px, $weight: 500, $color: #4fbfdb);
    text-decoration: underline;
  }
}

/* Technologies */
.tech__wrapper {
  @include flex($justify: start, $direct: column, $gap: 40px);
  min-height: 90vh;
  padding: 100px calc(5vw + 20px) 0;
}
.technologies {
  @include flex($gap: 30px, $wrap: wrap);
  img {
    width: 100px;
    height: 100px;
  }
}
.tech-wrap {
  max-width: 150px;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 7px;
  transition: all 0.5s;
}
.tech-item-name {
  font-size: calc(10px + 0.5vw);
  font-weight: 600;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.5s, opacity 0.5s;
}
.tech-wrap:hover .tech-item-name {
  visibility: visible;
  opacity: 1;
}

/* Projects */
.projects-section__wrapper {
  @include flex($direct: column, $gap: 40px);
  padding: 100px calc(5vw + 20px) 0;
}
.projects__wrapper {
  @include flex($direct: column, $gap: 22px);
  width: 100%;
}
.project {
  /**/
  @include flex($justify: center, $direct: column, $gap: 10px);
  width: 100%;
}
.project-text {
  @include flex($align: center, $direct: column, $gap: 24px);
  text-align: center;
  border-radius: 5px 5px 0px 0px;
  width: 100%;
  padding: 20px 35px;
}
.project-text2 {
  @include flex($align: start, $direct: column, $gap: 24px);
  border-radius: 5px 5px 0px 0px;
  border: 1px solid #ced4da;
  background: #f8f8f9;
  width: 100%;
  padding: 20px 35px;
  p {
    text-align: left;
  }
}
.tech-stack {
  @include font(22px, 500, #000);
}
.description {
  @include font(20px, 500, #000);
}
.details {
  color: #1518d1;
  font-size: 18px;
}
.project-links {
  @include flex($direct: row, $gap: calc(20vw + 20px));
  a {
    img {
      width: 70px;
      height: 70px;
    }
  }
  border-radius: 0px 0px 5px 5px;
  padding: 10px 35px;
  width: 100%;
}

/* Contacts */
.contacts__wrapper {
  min-height: 90vh;
  @include flex($justify: start, $direct: column, $gap: 40px);
  padding: 100px calc(5vw + 20px) 0;
  .bmc {
    height: 60px;
  }
}
.contacts_socials {
  @include flex($direct: row, $gap: 30px);
  img {
    width: 100px;
    height: 100px;
  }
}
.contact-item-wrap {
  max-width: 150px;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 7px;
  transition: all 0.5s;
}
.contact-name {
  font-size: calc(10px + 0.5vw);
  font-weight: 600;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.5s, opacity 0.5s;
}
.contact-item-wrap:hover .contact-name {
  visibility: visible;
  opacity: 1;
}

@keyframes spinner {
    to {
        transform: rotate(360deg)
    }
}

.loading {
    width: 6rem;
    height: 6rem;
    border: 5px solid #000000;
    border-radius: 50%;
    margin: 0 auto;
    border-top-color: #ffffff;
    animation: spinner .3s linear infinite
}

/* Work experience */
.work__wrapper {
  min-height: 90vh;
  @include flex($justify: start, $direct: column, $gap: 40px);
  padding: 100px calc(5vw + 20px) 0;
}
.work {
  padding: calc(2vw + 10px) calc(3vw + 20px);
  border-radius: 22px;
  background: #ffffff;
  box-shadow: 0px 0px 12px 0px rgba(0, 0, 0, 0.226);
  @include flex($direct: column, $gap: 10px);
  text-align: center;
  h3 {
    font-weight: 700;
  }
  p {
    font-size: 20px;
    font-weight: 600;
  }
}
.position {
  @include flex($direct: column, $gap: 10px);
}

/* Error page */
.error__wrapper {
  height: 80vh;
  @include flex($justify: start, $direct: column, $gap: 40px);
  padding: 100px calc(5vw + 20px) 0;
  img {
    width: 100px;
    height: 100px;
  }
}
.error-link {
  color: #580055;
}

/* Footer */
footer {
  background: #0d0c16;
}
.footer__wrapper {
  @include flex($direct: column);
  margin-top: 100px;
  padding: 20px 0;
  p {
    @include font(18px, 400, #fff);
  }
}

/*slider*/
.slider-container {
  margin: 0 auto;
  margin-top: 2rem;
  width: 100vw;
  max-width: 800px;
  position: relative;
  height: 450px;
  overflow: hidden;
}

.slide {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transition: var(--transition);
}

.slide-img {
  width: calc(500px + 10vw);
  height: calc(500px + 10vw);
  border-radius: 5px;
  margin-bottom: 1rem;
  object-fit: cover;
  border: 4px solid var(--primary-200);
  box-shadow: var(--shadow-3);
  object-fit: cover;
  border: 2px solid black;
}

/* Responsive */

@media (max-width: 1160px) {
  .hamburger {
    display: block;
  }
  .hamburger.active .bar:nth-child(2) {
    opacity: 0;
  }
  .hamburger.active .bar:nth-child(1) {
    transform: translateY(8px) rotate(45deg);
  }
  .hamburger.active .bar:nth-child(3) {
    transform: translateY(-8px) rotate(-45deg);
  }
  .nav-menu {
    position: fixed;
    left: -100%;
    top: 60px;
    gap: 0;
    flex-direction: column;
    background-color: #0d0c16;
    width: 100%;
    text-align: center;
    transition: 0.3s;
  }
  .nav-item {
    margin: 16px 0;
  }
  .nav-menu.active {
    left: 0;
  }
  /* Technologies */
  .tech-item-name {
    font-size: calc(17px + 0.5vw);
    visibility: visible;
    opacity: 1;
  }
  /* Contacts */
  .contact-name {
    font-size: calc(17px + 0.5vw);
    visibility: visible;
    opacity: 1;
  }
}
@media (max-width: 991px) {
  h2 {
    @include font(34px, 600, #000);
    text-align: center;
  }
  h3 {
    @include font(22px, 600, #000);
    text-align: center;
  }
  /* Main */
  .main__wrapper {
    @include flex($direct: column, $justify: start, $gap: calc(10vw + 20px));
    padding: calc(5vh + 20px) calc(5vw + 20px);
  }
  .main-text {
    text-align: center;
    @include flex(
      $direct: column,
      $align: center,
      $justify: center,
      $gap: 30px
    );
    h1 {
      @include font(45px, 700, #212529);
    }
    p {
      @include font(35px, 600, #212529);
    }
  }
  .main-image {
    img {
      width: 300px;
      height: 300px;
    }
  }
  /* About */
  .about__wrapper {
    p {
      @include font(17px, 400, #000);
    }
  }

  /* Projects */
  .project-text {
    text-align: center;
    @include flex($align: center, $direct: column, $gap: 17px);
    padding: 15px 10px;
  }
  .tech-stack {
    @include font(20px, 500, #000);
  }
  .description {
    @include font(16px, 500, #000);
  }
  .project-links {
    @include flex($direct: row, $gap: calc(10vw + 20px));
    a {
      img {
        width: 50px;
        height: 50px;
      }
    }
    padding: 10px 0;
  }
  /* Contacts */
  .contacts__wrapper {
    @include flex($direct: column, $gap: 30px, $justify: start);
  }
  .contacts_socials {
    @include flex($direct: row, $gap: 30px, $wrap: wrap);
    img {
      width: 70px;
      height: 70px;
    }
  }

  /* Footer */
  .footer__wrapper {
    padding: 23px 0;
    p {
      @include font(14px, 400, #fff);
    }
  }
  /* Slider */
  .slide-img {
    width: 90vw;
    height: 90vw;
  }
}
